import React from "react";
import "./App.css";
import {
  BrowserRouter as Router,
  Route,
  Link,
  Redirect,
  withRouter
} from "react-router-dom";
import Header from "./components/Header/index";
import Proposal from "./proposal/index";
import ClosedProposal from "./history/index";
import VoteProposal from "./vote/index";
import Contract from "./contract/index"
import Transfer from "./transfer/index"

function App() {
  return (
    <div>
      <Router>
        <Header />
        <Route exact path="/governance" component={Proposal} />
        <Route path="/governance/history" component={ClosedProposal} />
        <Route path="/governance/vote" component={VoteProposal} />
        <Route path="/governance/transfer" component={Transfer} />
        <Route path="/governance/contract" component={Contract} />
      </Router>
    </div>
  );
}

export default App;
