import ky from 'ky'

export const BaseUrl = {
    nas:
        process.env.API_ENV === 'prod'
            ? 'https://mainnet.nebulas.io'
            : 'https://testnet.nebulas.io',
    nas_explorer:
        process.env.API_ENV === 'prod'
            ? 'https://explorer.nebulas.io/#/'
            : 'https://explorer.nebulas.io/#/testnet/',
};



export const getAccountState = params => simplePost(`${BaseUrl.nas}/v1/user/accountstate`,params);

export const contractCall = params => simplePost(`${BaseUrl.nas}/v1/user/call`,params);

//////////////////////////////////////////// GET POST //////////////////////////////////////////////////////

export function simplePost(url,params) {
    return ky.post(url, {json: params}).json();
}

export function simpleGet(url) {
    return ky.get(url).json();
}